import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { styles } from "./styles";

const Backdrop = ({ classes, img, content }) => {
    return (
        <div className={classes.root}>
            <span className={classes.img}>{img}</span>
            <span className={classes.content}>{content}</span>
            <span className={classes.backdrop} />
        </div>
    );
};

Backdrop.propTypes = {
    classes: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    img: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
    content: PropTypes.oneOfType([PropTypes.object, PropTypes.string]).isRequired,
};

export default withStyles(styles, { withTheme: true })(Backdrop);
